import React, { useState, useEffect, useRef } from 'react'
import Tag from '../../Tag'
import { TagVariants } from '../../../constants'

const MultiSelect = ({ id, values, onSelect, currentValue }) => {
    const [actives, setActives] = useState(currentValue || [])

    const setInitial = useRef(false)
    useEffect(() => {
        if (currentValue && !setInitial.current && currentValue !== actives) {
            setInitial.current = true
            setActives(currentValue)
        }
    }, [currentValue])

    const handleClick = valueId => {
        let newValues = []
        if (!actives.includes(valueId)) {
            newValues = [...actives, valueId]
        } else {
            newValues = actives.filter(a => a !== valueId)
        }
        setActives(newValues)
        onSelect(newValues)
    }

    return (
        <div id={id}>
            {values.map(v => (
                <Tag
                    onClick={() => handleClick(v.id)}
                    variant={actives.includes(v.id) ? TagVariants.DARK : null}
                    key={v.id}
                >
                    {v.label}
                </Tag>
            ))}
        </div>
    )
}

export default MultiSelect
