import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import { borderColorValue, borderWidthValue } from '../../constants/cssRules'

const StyledTableItem = styled.article`
    ${props => `
    width: 100%;
    display: block;
    padding: 10px 0;
    border-bottom: ${borderWidthValue()} ${
        props.noLine ? 'transparent' : borderColorValue(props)
    } solid;
`}
`

const TableItem = ({ className, id, children, noLine }) => {
    const themeContext = useContext(ThemeContext)
    return (
        <StyledTableItem
            className={className}
            id={id}
            themeContext={themeContext}
            noLine={noLine}
        >
            {children}
        </StyledTableItem>
    )
}

export default TableItem
