import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import ComponentError from '../States/ComponentError'
import { Link } from 'react-router-dom'
import { colorRule } from '../../constants/cssRules'

const StyledBreadcrumbs = styled.div``

const StyledBreadcrumbValue = styled.span`
    font-size: 12px;
    ${props => colorRule(props)}
    ${props =>
        props.active
            ? `
    font-weight: bolder;
    color: ${props.themeContext.secondary}`
            : ''}
    cursor: pointer;
`
const StyledBreadcrumbSeparator = styled.span`
    font-size: 11px;
    margin: 0 10px;
    cursor: default;
    ${props => colorRule(props)}
`

const Breadcrumb = React.memo(
    ({ linkValue, children, isLastOne, active, themeContext, linkBase }) => {
        const getBreadcrumbContent = () => (
            <span className='rds-flexbox align-center '>
                <StyledBreadcrumbValue
                    themeContext={themeContext}
                    active={active}
                >
                    {children}
                </StyledBreadcrumbValue>
                {isLastOne ? null : (
                    <StyledBreadcrumbSeparator themeContext={themeContext}>
                        {'>'}
                    </StyledBreadcrumbSeparator>
                )}
            </span>
        )
        if (linkBase) {
            return (
                <Link to={`${linkBase}${linkValue}`}>
                    {getBreadcrumbContent()}
                </Link>
            )
        }
        return getBreadcrumbContent()
    },
)

const Breadcrumbs = ({ values, linkBase, current, className }) => {
    const themeContext = useContext(ThemeContext)
    if (!values) {
        return (
            <ComponentError
                componentName='Breadcrumbs'
                errorMessage='A "values" prop needs to be provided'
            />
        )
    }
    return (
        <StyledBreadcrumbs
            className={`rds-flexbox align-center ${className || ''}`}
            themeContext={themeContext}
        >
            {values.map((v, i) => (
                <Breadcrumb
                    key={v.linkValue}
                    active={v.label === current}
                    isLastOne={i === values.length - 1}
                    themeContext={themeContext}
                    linkBase={linkBase}
                    linkValue={v.linkValue}
                >
                    {v.label}
                </Breadcrumb>
            ))}
        </StyledBreadcrumbs>
    )
}

export default Breadcrumbs
