import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import {
    darkBackgroundRule,
    colorRule,
    borderRadiusRule,
} from '../../constants/cssRules'

const StyledTextarea = styled.textarea`
    ${props => darkBackgroundRule(props)}
    ${props => colorRule(props)}
    ${props => borderRadiusRule(props)}
`

const Textarea = ({
    id,
    onChange,
    onFocus,
    onBlur,
    required,
    placeholder,
    value,
    className,
}) => {
    const themeContext = useContext(ThemeContext)
    return (
        <StyledTextarea
            themeContext={themeContext}
            autoComplete='off'
            id={id}
            onChange={onChange}
            onFocus={onFocus}
            onBlur={onBlur}
            required={required}
            value={value}
            placeholder={placeholder}
            className={className || ''}
        />
    )
}

export default Textarea
