import React from 'react'
import { ThemeVariants } from '../constants'
import {
    BlackBackground,
    DarkDiv,
    MainBackgroundDiv,
    MainColorBGDiv,
} from './Divs'

const Block = ({
    id,
    children,
    className,
    style,
    align,
    onClick,
    roundedBorder,
    variant,
}) => {
    switch (variant) {
        case ThemeVariants.MAIN:
            return (
                <MainColorBGDiv
                    id={id}
                    children={children}
                    className={className}
                    style={style}
                    align={align}
                    onClick={onClick}
                    roundedBorder={roundedBorder}
                />
            )
        case ThemeVariants.DARK:
            return (
                <BlackBackground
                    id={id}
                    children={children}
                    className={className}
                    style={style}
                    align={align}
                    onClick={onClick}
                    roundedBorder={roundedBorder}
                />
            )
        case ThemeVariants.GRAY:
            return (
                <DarkDiv
                    id={id}
                    children={children}
                    className={className}
                    style={style}
                    align={align}
                    onClick={onClick}
                    roundedBorder={roundedBorder}
                />
            )
        default:
            return (
                <MainBackgroundDiv
                    id={id}
                    children={children}
                    className={className}
                    style={style}
                    align={align}
                    onClick={onClick}
                    roundedBorder={roundedBorder}
                />
            )
    }
}

export default Block
