import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import {
    backgroundRule,
    borderRule,
    borderRadiusRule,
} from '../../constants/cssRules'
import Header from '../Header'

export const StyledCard = styled.article`
    ${props =>
        props.themeContext.main &&
        `
    ${backgroundRule(props)}
    ${borderRule(props)}
    ${props.noBorderRadius ? '' : borderRadiusRule(props)}
    `}
    position: relative;
    padding: 18px 20px;
    overflow: hidden;
    transition: 0.2s background-color;
`

const Card = ({
    children,
    className,
    onClick,
    style,
    onDoubleClick,
    onMouseOver,
    onMouseLeave,
    variant,
    id,
    noBorderRadius,
    align,
    headerTitle,
    headerText,
    headerSize,
    headerVariant,
    headerIcon,
}) => {
    const themeContext = useContext(ThemeContext)
    return (
        <StyledCard
            style={{
                ...style,
                cursor: onClick ? 'pointer' : 'unset',
            }}
            themeContext={themeContext}
            className={`card ${className || ''}`}
            onClick={onClick}
            onDoubleClick={onDoubleClick}
            onMouseOver={onMouseOver}
            onMouseLeave={onMouseLeave}
            variant={variant}
            id={id}
            align={align}
            noBorderRadius={noBorderRadius}
        >
            {headerTitle ? (
                <Header
                    title={headerTitle}
                    text={headerText}
                    size={headerSize}
                    variant={headerVariant}
                    icon={headerIcon}
                />
            ) : null}
            {children}
        </StyledCard>
    )
}

export default Card
