import { hexDecrement } from '../utils'
import { ThemeVariants, ThemesNames } from '.'

export const SIDEBAR_ICON_WIDTH = `17px`
export const COLOR_ERROR = `#f90813`
export const COLOR_WARNING = `#f9b700`
export const COLOR_WHITE = `#ffffffd9`

export const mainBackgroundValue = props =>
    props.themeContext.scheme === ThemesNames.DARK ? `#1d1d1d` : `#fbfbfb`

export const mainBackgroundRule = props =>
    props.themeContext.main && `background: ${mainBackgroundValue(props)};`

export const blackValue = theme =>
    theme.scheme === ThemesNames.DARK ? '#0f0f0f' : '#171f2f'

export const borderColorValue = props => {
    if (props.variant) {
        switch (props.variant) {
            case ThemeVariants.SECONDARY_LIGHT:
                return props.themeContext.secondary
            case ThemeVariants.SECONDARY:
                return props.themeContext.secondary
            case ThemeVariants.ERROR:
                return COLOR_ERROR
            case ThemeVariants.WARNING:
                return COLOR_WARNING
            case ThemeVariants.GLOSSY:
                return 'transparent'
            default:
                break
        }
    } else {
        if (props.themeContext.scheme === ThemesNames.DARK) {
            return '#464646'
        }
        return '#ececec'
    }
}

export const borderWidthValue = () => `1px`

export const borderValue = props =>
    `${borderWidthValue()} ${borderColorValue(props)} solid`

export const borderRule = props => {
    let rule = `border: `
    switch (props.variant) {
        case ThemeVariants.GLOSSY:
            rule += 'unset'
            break
        default:
            rule += borderValue(props)
            break
    }
    return `${rule} !important;`
}

export const borderRadiusValue = props =>
    `${props.themeContext.border_radius || 5}px`

export const borderRadiusRule = props =>
    `border-radius: ${borderRadiusValue(props)};`

export const mainColorBackgroundRule = props =>
    `background: ${props.themeContext.main};`

export const lightBackgroundRule = props =>
    `${
        props.themeContext.main &&
        `background: ${
            props.themeContext.scheme === ThemesNames.DARK
                ? `#ffffff20`
                : `white`
        };`
    }`

export const darkBackgroundRule = props =>
    `${
        props.themeContext.main &&
        `background: ${
            props.themeContext.scheme === ThemesNames.DARK
                ? `#2a2a2a`
                : `${hexDecrement(props.themeContext.main, 100)}08`
        };`
    }`

export const blackBackgroundRule = props =>
    `background: ${blackValue(props.themeContext)};`

export const backgroundRule = props => {
    let colorValue = ''
    if (props.variant) {
        switch (props.variant) {
            case ThemeVariants.DARK:
                colorValue = blackValue(props.themeContext)
                break
            case ThemeVariants.SECONDARY:
                colorValue = props.themeContext.secondary
                break
            case ThemeVariants.SECONDARY_LIGHT:
                colorValue = `${hexDecrement(
                    props.themeContext.secondary,
                    40,
                )}2e`
                break
            case ThemeVariants.WARNING:
                colorValue = `${COLOR_WARNING}2e`
                break
            case ThemeVariants.ERROR:
                colorValue = `${COLOR_ERROR}2e`
                break
            case ThemeVariants.MAIN:
                colorValue = props.themeContext.main
                break
            case ThemeVariants.GLOSSY:
                colorValue = `linear-gradient(45deg, ${props.themeContext.main}, ${props.themeContext.secondary})`
                break
            default:
                break
        }
    } else {
        if (props.themeContext.scheme === ThemesNames.DARK) {
            colorValue = `#343434`
        } else {
            colorValue = 'white'
        }
    }
    return `background: ${colorValue};`
}

export const inputBackgroundRule = props =>
    `background: ${
        props.themeContext.scheme === ThemesNames.DARK ? `#3d3d3d` : `#f8f8f8`
    };`

export const secondaryBackgroundRule = props =>
    `background: ${props.themeContext.secondary};`

export const colorValue = props => {
    let colorValue = ''
    switch (props.variant) {
        case ThemeVariants.MAIN:
            colorValue = hexDecrement(props.themeContext.main, 15)
            break
        case ThemeVariants.SECONDARY:
            colorValue = hexDecrement(props.themeContext.secondary, 15)
            break
        case ThemeVariants.ERROR:
            colorValue = COLOR_ERROR
            break
        case ThemeVariants.WARNING:
            colorValue = COLOR_WARNING
            break
        case ThemeVariants.WHITE:
            colorValue = `#ffffffd9`
            break
        case ThemeVariants.DARK:
            colorValue = blackValue(props.themeContext)
            break
        default:
            colorValue =
                props.themeContext.scheme === ThemesNames.DARK
                    ? COLOR_WHITE
                    : blackValue(props.themeContext)
            break
    }
    if (props.variant) {
        colorValue += ` !important`
    }
    return colorValue
}

export const colorRule = props => {
    const resultantColorValue = colorValue(props)
    return `
        color: ${resultantColorValue};
        & * {
            color: ${resultantColorValue};
        }
    `
}

export const labelColorRule = props =>
    `color: ${
        props.themeContext.scheme === ThemesNames.DARK ||
        props.variant === ThemeVariants.WHITE
            ? `#afafaf`
            : `#757575`
    };`

export const softShadowRule = props =>
    `box-shadow: ${
        props.themeContext.scheme === ThemesNames.DARK
            ? // No soft shadow in DARK MODE
              `unset`
            : props.variant !== ThemeVariants.SECONDARY
            ? `0 10px 35px -15px ${blackValue(props.themeContext)}15`
            : // No soft shadow when its a variant = SECONDARY
              `unset`
    };`

export const fontSizeRule = fontSize => `
    font-size: ${fontSize};
    & * {
        font-size: ${fontSize};
    }
`
