import React from 'react'
import Card from './../Card'
import Header from '../Header'
import { HeaderSizes } from '../../constants'

const FormSection = ({ children, size, title, description }) => {
    return (
        <div className={`form-section ${size}`}>
            <Card
                style={{
                    margin: '0 0 15px 0',
                    width: '100%',
                }}
            >
                {title ? (
                    <Header
                        title={title}
                        text={description}
                        size={HeaderSizes.SMALL}
                    />
                ) : null}
                <div>{children}</div>
            </Card>
        </div>
    )
}

export default FormSection
