import React from 'react'
import Icon from '../Icons'
import Header from '../Header'
import { Icons, HeaderSizes, ThemeVariants } from '../../constants'

const EmptyState = ({ title, message, centered, alignLeft, style }) => (
    <div
        className={`rds-state empty ${centered ? 'rds-centered' : ''}`}
        align={alignLeft ? '' : 'center'}
        style={style}
    >
        <Icon icon={Icons.EMPTY} background variant={ThemeVariants.SECONDARY} />
        <Header
            title={title}
            text={message}
            size={HeaderSizes.SMALL}
            alignCenter={!alignLeft}
        />
    </div>
)

export default EmptyState
