import React, { useEffect, useState, useRef } from 'react'
import Card from '../../Card'
import Tag from '../../Tag'
import Header from '../../Header'
import { DarkDiv } from '../../Divs'
import { TagVariants, ThemeVariants, HeaderSizes } from '../../../constants'
import ComponentError from '../../States/ComponentError'

const SelectOption = ({ value, currentValue, onSelect, cardsUI }) => {
    const [active, setActive] = useState(false)

    const currentValueSelect = useRef(false)

    // This hook handles the first render of the option
    // if it is the currentValue option, it will
    // trigger the onSelect callback so the upper
    // components have their fields populated
    useEffect(() => {
        if (active && !currentValueSelect.current) {
            currentValueSelect.current = true
            onSelect(value.id)
        }
    }, [active])

    useEffect(() => {
        if (
            currentValue !== null &&
            currentValue.toString() === value.id.toString()
        ) {
            setActive(true)
        } else {
            setActive(false)
        }
    }, [value, currentValue])

    if (cardsUI) {
        return (
            <Card
                title={value.id}
                className='form-custom-select_option'
                onClick={() => onSelect(value.id)}
                variant={active ? ThemeVariants.DARK : null}
            >
                <Header
                    title={value.label}
                    text={value.description}
                    variant={active ? ThemeVariants.WHITE : null}
                    size={HeaderSizes.XSMALL}
                />
            </Card>
        )
    }
    return (
        <Tag
            className='form-custom-select_option'
            key={value.label}
            title={value.id}
            variant={active ? null : TagVariants.TRANSPARENT}
            onClick={() => onSelect(value.id)}
        >
            {value.label}
        </Tag>
    )
}

const Select = ({ id, values, currentValue, onSelect, cardsUI }) => {
    if (!values) {
        return (
            <ComponentError
                componentName='Select'
                errorMessage='A "values" prop is needed'
            />
        )
    }
    return (
        <DarkDiv
            roundedBorder
            id={id}
            className={`form_select ${
                cardsUI
                    ? `rds-full-block rds-grid rds-grid_${values.length}-columns`
                    : 'rounded-border_full'
            }`}
            style={cardsUI ? { padding: '5px' } : null}
        >
            {values.map((value, i) => (
                <SelectOption
                    key={i}
                    value={value}
                    currentValue={currentValue}
                    onSelect={onSelect}
                    cardsUI={cardsUI}
                />
            ))}
        </DarkDiv>
    )
}

export default React.memo(Select)
