import React, { useState } from 'react'
import styled from 'styled-components'
import Label from '../Label'
import Typography from '../Typography'
import { DarkDiv } from '../Divs'
import { Link } from 'react-router-dom'
import ErrorState from './../States/ErrorState'
import EmptyState from './../States/EmptyState'
import NoImage from './../States/NoImage'
import { TypographyTypes, ButtonVariants, HeaderSizes } from '../../constants'
import Tag from '../Tag'
import Card from '../Card'
import Header from '../Header'
import Button from '../Button'
import Loading from '../States/Loading'
import ComponentError from '../States/ComponentError'
import TableItem from './TableItem'
import { borderRadiusRule } from '../../constants/cssRules'
import ButtonGroup from '../Button/ButtonGroup'

const StyledTableImg = styled.img`
    ${props => borderRadiusRule(props)}
`

const Table = ({
    addRowAction,
    refreshAction,
    className,
    title,
    description,
    columnNames,
    rows,
    fetching,
}) => {
    const [imgError, setImgError] = useState(false)

    const getNestedValue = (item, fieldPath) => {
        let value
        const splitted = fieldPath.split('.')
        splitted.forEach((split, i) => {
            value = i === 0 ? item[split] : value ? value[split] : null
        })
        return value
    }

    const getRowValue = (item, columnName) => {
        const { options } = columnName
        let formattedValue
        let jsxValue

        // Get actual inner value
        if (columnName.fieldName.includes('.')) {
            // This means the value is nested in some object
            formattedValue = getNestedValue(item, columnName.fieldName)
        } else {
            // Check if it is an array
            if (
                columnName.fieldName &&
                item[columnName.fieldName] &&
                typeof item[columnName.fieldName] === 'object'
            ) {
                formattedValue =
                    item[columnName.fieldName].length !== null &&
                    item[columnName.fieldName].length !== undefined
                        ? item[columnName.fieldName].length
                        : "In order to use object properties, use '<object>.<property>' to access the value."
            } else {
                formattedValue = item[columnName.fieldName]
            }
        }

        if (!options) {
            return (
                <Typography type={TypographyTypes.P}>
                    {formattedValue}
                </Typography>
            )
        }

        // Handle API options
        const {
            boolean,
            image,
            dateValue,
            prefix,
            suffix,
            tagged,
            link,
            action,
        } = options

        // Date value?
        if (dateValue) {
            formattedValue = `${new Date(
                formattedValue,
            ).toLocaleDateString()} at ${new Date(
                formattedValue,
            ).toLocaleTimeString()} 📅`
        }

        // Prefix or Suffix?
        if (prefix) {
            formattedValue = prefix + ' ' + formattedValue
        }
        if (suffix) {
            formattedValue = formattedValue + ' ' + suffix
        }

        jsxValue = formattedValue

        // Boolean?
        if (boolean) {
            jsxValue = jsxValue ? boolean.values.true : boolean.values.false
        }

        // Image?
        if (image) {
            return !imgError && formattedValue ? (
                <StyledTableImg
                    onClick={() => action && action(item)}
                    className='table_img'
                    src={formattedValue}
                    alt=''
                    onError={() => setImgError(true)}
                />
            ) : (
                <NoImage width={30} />
            )
        }

        // Tagged?
        if (tagged) {
            jsxValue = (
                <Tag
                    onClick={action ? () => action(item) : null}
                    style={
                        boolean &&
                        boolean.falseState &&
                        boolean.values.false &&
                        !formattedValue
                            ? {
                                  filter: 'grayscale(1)',
                                  opacity: '0.7',
                              }
                            : null
                    }
                    className={link ? 'hoverable' : null}
                    variant={
                        !boolean ||
                        !boolean.falseState ||
                        formattedValue !== false
                            ? tagged.dynamicVariant
                                ? tagged.dynamicVariant(formattedValue)
                                : tagged.variant
                            : null
                    }
                >
                    {jsxValue}
                </Tag>
            )
        } else {
            jsxValue = (
                <Typography type={TypographyTypes.P}>
                    {formattedValue}
                </Typography>
            )
        }

        // Link?
        if (link && link.recordFieldName) {
            let formattedLinkValue = item[link.recordFieldName]
            if (link.recordFieldName.includes('.')) {
                formattedLinkValue = getNestedValue(item, link.recordFieldName)
            }
            jsxValue = (
                <Link
                    onClick={!tagged && action ? () => action(item) : null}
                    to={`${link.base}${formattedLinkValue}`}
                >
                    {jsxValue}
                </Link>
            )
        }

        return jsxValue
    }

    // Disabled by now
    // const getColumnPosition = (position, columns) =>
    //     position !== 0
    //         ? position !== columns.length - 1
    //             ? 'center'
    //             : 'right'
    //         : null

    if (!columnNames) {
        return (
            <ComponentError
                componentName='Table'
                errorMessage="The 'columnNames' prop is required"
            />
        )
    }
    if (columnNames.length > 6) {
        return (
            <ErrorState
                title='Table error'
                message="There's a maximum of 6 columns."
            />
        )
    }
    return (
        <Card className={`rds-table ${className}`}>
            {title || addRowAction || refreshAction ? (
                <div className='table_header rds-flexbox align-center justify-between'>
                    {title ? (
                        <Header
                            title={title}
                            text={description}
                            size={HeaderSizes.SMALL}
                        />
                    ) : null}
                    <div className='rds-flexbox align-center'>
                        {/* TODO: Implement a search mechanism in Tables */}
                        {/* <SearchBox style={{ margin: '-10px 10px 0 0' }} /> */}
                        {addRowAction ? (
                            <Link to={addRowAction.link}>
                                <ButtonGroup>
                                    <Button
                                        variant={ButtonVariants.ADD}
                                        label={addRowAction.label}
                                    />
                                </ButtonGroup>
                            </Link>
                        ) : null}
                        {refreshAction ? (
                            <ButtonGroup style={{ margin: '0 0 0 10px' }}>
                                <Button
                                    variant={ButtonVariants.RESTORE}
                                    onClick={refreshAction}
                                />
                            </ButtonGroup>
                        ) : null}
                    </div>
                </div>
            ) : null}
            <div
                className={`rds-table_column-headers rds-full-block ${
                    title ? '' : ''
                } rds-grid rds-table_grid_${columnNames.length}-column${
                    columnNames.length > 1 ? 's' : ''
                }`}
            >
                {columnNames.map((columnName, i) => (
                    <div
                        key={i}
                        // align={getColumnPosition(i, columnNames)}
                    >
                        <Label>{columnName.label}</Label>
                    </div>
                ))}
            </div>
            {fetching ? (
                <div className='rds-full-block rds-m_top__md' align='center'>
                    <Loading tag='Fetching data...' />
                </div>
            ) : (
                <DarkDiv className='rds-full-block rds-m_top__sm'>
                    <Card className='rds-full-block table_content'>
                        {rows.length ? (
                            rows.map((item, i) => (
                                <TableItem
                                    key={i}
                                    noLine={i === rows.length - 1}
                                    className={`rds-grid rds-table_grid_${columnNames.length}-columns rds-flexbox align-top`}
                                >
                                    {columnNames.map((columnName, i) => (
                                        <div
                                            key={i}
                                            // align={getColumnPosition(
                                            //     i,
                                            //     columnNames,
                                            // )}
                                        >
                                            {getRowValue(item, columnName)}
                                        </div>
                                    ))}
                                </TableItem>
                            ))
                        ) : (
                            <EmptyState title='Nothing here' />
                        )}
                    </Card>
                </DarkDiv>
            )}
        </Card>
    )
}

export default Table
