import React from 'react'
import styled from 'styled-components'
import { colorRule, fontSizeRule } from '../../constants/cssRules'

const StyledH3 = styled.h3`
    ${props => `
        ${colorRule(props)}
        ${fontSizeRule('1rem')}
        font-weight: 300;
    `}
`

const H3 = ({
    themeContext,
    className,
    children,
    align,
    onClick,
    variant,
    style,
}) => {
    return (
        <StyledH3
            align={align}
            themeContext={themeContext}
            className={className}
            onClick={onClick && onClick}
            style={onClick ? { cursor: 'pointer', ...style } : style}
            variant={variant}
        >
            {children}
        </StyledH3>
    )
}

export default H3
