import React, { useState, useEffect } from 'react'
import { Icons } from '../../../constants'
import Button from '../../Button'
import FilesPickerFile from './FilesPickerFile'

const FilesPicker = ({
    accept,
    label,
    uploadLabel,
    multiple,
    initialFiles,
    onChange,
    onRemove,
}) => {
    const [files, setFiles] = useState(initialFiles || [])

    useEffect(() => {
        if (onChange) {
            onChange(files)
        }
    }, [files])

    const handleFileSelect = e => {
        if (e.target.files.length) {
            const currentFile = e.target.files[e.target.files.length - 1]
            const formattedFile = {
                filename: currentFile.name,
                file: currentFile,
                preview: URL.createObjectURL(currentFile),
            }
            if (!multiple) {
                setFiles([formattedFile])
                return
            }
            setFiles([...files, formattedFile])
        }
    }

    const handleAddFileClick = () =>
        document.getElementById('pictures-picker-input').click()

    const handleRemoveFileClick = fileRemoved => {
        setFiles(files.filter(f => f !== fileRemoved))
        if (onRemove) {
            onRemove(fileRemoved)
        }
    }

    return (
        <div className='rds-m_top__sm'>
            <div style={{ whiteSpace: 'nowrap', overflowX: 'auto' }}>
                {files.map((f, i) => (
                    <FilesPickerFile
                        removeCallback={() => handleRemoveFileClick(f)}
                        fileData={f}
                        key={i}
                    />
                ))}
            </div>
            <div>
                <input
                    accept={accept}
                    id='pictures-picker-input'
                    onChange={handleFileSelect}
                    style={{ display: 'none' }}
                    multiple={multiple ? 'multiple' : null}
                    type='file'
                />
                <Button
                    icon={Icons.ADD}
                    label={uploadLabel || 'Upload a file'}
                    onClick={handleAddFileClick}
                />
            </div>
        </div>
    )
}

export default FilesPicker
