import React from 'react'
import styled from 'styled-components'
import { colorRule, fontSizeRule } from '../../constants/cssRules'

const StyledH1 = styled.h1`
    ${props => {
        const FONT_SIZE = '2rem'
        return `
            ${colorRule(props)}
            ${fontSizeRule(FONT_SIZE)}
        `
    }}
`

const H1 = ({
    themeContext,
    className,
    children,
    align,
    onClick,
    variant,
    style,
}) => {
    return (
        <StyledH1
            align={align}
            themeContext={themeContext}
            className={className}
            onClick={onClick && onClick}
            style={onClick ? { cursor: 'pointer', ...style } : style}
            variant={variant}
        >
            {children}
        </StyledH1>
    )
}

export default H1
