import React from 'react'
import Typography from '../Typography'
import { TypographyTypes, EmojiTypes } from '../../constants'
import { Emoji } from '../..'
import Card from '../Card'

const ComponentError = ({ componentName, errorMessage }) => {
    return (
        <Card className='rds-m_top__sm'>
            <Typography type={TypographyTypes.P}>
                <Emoji type={EmojiTypes.WARNING} /> {componentName} component
                error
            </Typography>
            <Typography type={TypographyTypes.H4}>{errorMessage}</Typography>
        </Card>
    )
}

export default React.memo(ComponentError)
