import React, { useContext } from 'react'
import H1 from './H1'
import H2 from './H2'
import H3 from './H3'
import H4 from './H4'
import P from './P'
import { TypographyTypes } from './../../constants'
import { ThemeContext } from 'styled-components'
import ComponentError from '../States/ComponentError'

const Typography = ({ type, children, className, variant, style }) => {
    const themeContext = useContext(ThemeContext)
    const getTypography = () => {
        switch (type) {
            case TypographyTypes.H1:
                return (
                    <H1
                        themeContext={themeContext}
                        style={style}
                        className={className}
                        variant={variant}
                    >
                        {children}
                    </H1>
                )
            case TypographyTypes.H2:
                return (
                    <H2
                        themeContext={themeContext}
                        style={style}
                        className={className}
                        variant={variant}
                    >
                        {children}
                    </H2>
                )
            case TypographyTypes.H3:
                return (
                    <H3
                        themeContext={themeContext}
                        style={style}
                        className={className}
                        variant={variant}
                    >
                        {children}
                    </H3>
                )
            case TypographyTypes.H4:
                return (
                    <H4
                        themeContext={themeContext}
                        style={style}
                        className={className}
                        variant={variant}
                    >
                        {children}
                    </H4>
                )
            case TypographyTypes.P:
                return (
                    <P
                        themeContext={themeContext}
                        style={style}
                        className={className}
                        variant={variant}
                    >
                        {children}
                    </P>
                )
            default:
                return null
        }
    }

    if (!type) {
        return (
            <ComponentError
                componentName='Typography'
                errorMessage='A valid "type" property needs to be provided'
            />
        )
    }
    return getTypography()
}

export default React.memo(Typography)
