import React from 'react'
import Isologo from './../Logo/Isologo'
import Typography from '../Typography'
import { TypographyTypes } from '../../constants'

const Loading = ({ tag, fetching, className }) => {
    const getAnimation = () => (
        <div className='loading'>
            <Isologo />
        </div>
    )

    if (tag)
        return (
            <div
                className={`${
                    className || ''
                } tagged-loading rds-flexbox align-center justify-center ${
                    fetching ? 'active' : ''
                }`}
            >
                {getAnimation()}
                <Typography type={TypographyTypes.P}>{tag}</Typography>
            </div>
        )
    return getAnimation()
}
export default Loading
