import React from 'react'
import { Icons, HeaderSizes, ThemeVariants } from '../../constants'
import Icon from '../Icons'
import Header from '../Header'

const ErrorState = ({ title, message, centered, style, alignLeft }) => (
    <div
        className={`rds-state ${centered ? 'rds-centered' : ''}`}
        align={alignLeft ? '' : 'center'}
        style={style}
    >
        <Icon
            icon={Icons.WARNING}
            background
            width={30}
            variant={ThemeVariants.SECONDARY}
        />
        <Header
            size={HeaderSizes.SMALL}
            title={title}
            text={message}
            alignCenter={!alignLeft}
        />
    </div>
)

export default ErrorState
