import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import {
    borderRule,
    backgroundRule,
    colorRule,
    blackBackgroundRule,
    borderRadiusRule,
} from '../constants/cssRules'
import { TagVariants } from '../constants'

const StyledTag = styled.span`
    ${props => colorRule(props)}
    ${props => (!props.variant ? borderRule(props) : '')}
    ${props => borderRadiusRule(props)}
    ${props => (!props.variant ? backgroundRule(props) : '')}
    ${props =>
        props.variant === TagVariants.DARK ? blackBackgroundRule(props) : ''}
    display: inline-block;
    line-height: 1;
    margin: 0 7px 5px 0;
    padding: 2px 15px;
    opacity: 1;
    transition: 0.4s transform, 2s opacity, 0.2s background-color, 0.2s padding;
    border-radius: 20px !important;
    border: 1.5px solid transparent;
`

const StyledTagContent = styled.span`
    font-size: 0.7rem;
    color: ${props =>
        props.variant === TagVariants.DARK ? 'white !important' : 'unset'};
`

const Tag = ({ className, children, variant, style, onClick }) => {
    const themeContext = useContext(ThemeContext)
    const processedClassName = `rds-tag ${className || ''} ${variant || ''} ${
        onClick ? 'hoverable' : ''
    }`

    const getTag = () => (
        <StyledTagContent
            style={onClick ? { cursor: 'pointer' } : {}}
            themeContext={themeContext}
            variant={variant}
        >
            {children}
        </StyledTagContent>
    )

    return (
        <StyledTag
            themeContext={themeContext}
            variant={variant}
            className={processedClassName}
            style={style}
            onClick={onClick}
        >
            {getTag()}
        </StyledTag>
    )
}

export default React.memo(Tag)
