import React from 'react'
import { Link } from 'react-router-dom'
import Tag from './../Tag'
import { HeaderSizes, Icons, ThemeVariants } from '../../constants'
import Header from '../Header'
import Icon from '../Icons'

const DeletedState = ({ title, message, centered, link, alignLeft, style }) => (
    <div
        className={`rds-state deleted ${centered ? 'rds-centered' : ''}`}
        align={alignLeft ? '' : 'center'}
        style={style}
    >
        <Icon
            background
            icon={Icons.BIN}
            width={30}
            variant={ThemeVariants.SECONDARY}
        />
        <Header
            size={HeaderSizes.SMALL}
            title={title || 'Unavailable'}
            text={message || 'This item has been dropped'}
        >
            {link ? (
                <Link to={link.url}>
                    <Tag>{link.label}</Tag>
                </Link>
            ) : null}
        </Header>
    </div>
)

export default DeletedState
