import React, { useEffect, useState } from 'react'
import Icon from '../Icons'
import { Icons, HeaderSizes, ThemeVariants } from '../../constants'
import Header from '../Header'

const NoImage = React.memo(({ width, height, alignLeft, title, message }) => {
    const [value, setValue] = useState(null)

    useEffect(() => {
        let processedWidth = 30
        if (width) {
            processedWidth = width
        }
        setValue(processedWidth)
    }, [width])

    return (
        <div className='state item-img item-img_no-result'>
            <Icon
                icon={Icons.PICTURE}
                width={value}
                background
                variant={ThemeVariants.SECONDARY}
            />
            {title ? (
                <Header
                    title={title}
                    text={message}
                    size={HeaderSizes.SMALL}
                    alignCenter={!alignLeft}
                />
            ) : null}
        </div>
    )
})

export default NoImage
