import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import {
    darkBackgroundRule,
    colorRule,
    borderRadiusRule,
} from '../../constants/cssRules'

const StyledInput = styled.input`
    ${props => darkBackgroundRule(props)}
    ${props => colorRule(props)}
    ${props => borderRadiusRule(props)}
    transition: 0.2s background-color;
`

const Input = ({
    id,
    style,
    className,
    onClick,
    onChange,
    type,
    min,
    value,
    minLength,
    required,
    placeholder,
    children,
    onBlur,
    onFocus,
}) => {
    const themeContext = useContext(ThemeContext)
    return (
        <StyledInput
            themeContext={themeContext}
            style={style}
            id={id}
            className={className || ''}
            onClick={onClick}
            onChange={onChange}
            type={type}
            min={min}
            value={value}
            minLength={minLength}
            required={required}
            placeholder={placeholder}
            children={children}
            onBlur={onBlur}
            onFocus={onFocus}
        />
    )
}

export default Input
