import React from 'react'
import Typography from './Typography'
import Icon from './Icons'
import HeaderBreadcrumb from './HeaderBreadcrumb'
import { TypographyTypes, HeaderSizes } from '../constants'

const Header = ({
    alignCenter,
    title,
    icon,
    text,
    size,
    onClick,
    children,
    variant,
    className,
    tag,
    breadcrumb,
    style,
}) => {
    const getIconSize = () => {
        switch (size) {
            case HeaderSizes.XSMALL:
                return 10
            case HeaderSizes.SMALL:
                return 12
            case HeaderSizes.MEDIUM:
                return 20
            // Default value is BIG
            default:
                return 25
        }
    }

    const getIcon = () => {
        const size = getIconSize()
        const formattedSize = `${size}px`
        const iconStyle = {
            width: formattedSize,
            minWidth: formattedSize,
            height: formattedSize,
            margin: `${size / 1.7}px 13px 0 0`,
        }
        return (
            <Icon
                className='header_icon'
                icon={icon}
                style={iconStyle}
                variant={variant}
            />
        )
    }

    const getBreadcrumb = () =>
        breadcrumb ? (
            <HeaderBreadcrumb
                link={breadcrumb.link}
                value={breadcrumb.value}
                withSeparator
            />
        ) : null

    const getTitle = () => {
        const breadcrumbContent = getBreadcrumb()
        let titleSize
        switch (size) {
            case HeaderSizes.XSMALL:
                titleSize = TypographyTypes.H4
                break
            case HeaderSizes.SMALL:
                titleSize = TypographyTypes.H3
                break
            case HeaderSizes.MEDIUM:
                titleSize = TypographyTypes.H2
                break
            default:
                titleSize = TypographyTypes.H1
                break
        }
        if (tag) {
            return (
                <div className={tag ? 'rds-flexbox align-center' : ''}>
                    <Typography
                        type={titleSize}
                        variant={variant}
                        onClick={onClick}
                    >
                        {breadcrumbContent}
                        {title}
                    </Typography>
                    {tag}
                </div>
            )
        }
        return (
            <Typography type={titleSize} variant={variant} onClick={onClick}>
                {breadcrumbContent}
                {title}
            </Typography>
        )
    }

    const getContent = () => {
        return (
            <React.Fragment>
                {getTitle()}
                {children}
            </React.Fragment>
        )
    }

    return (
        <header
            className={`main-header ${
                alignCenter ? 'center' : ''
            } ${className}`}
            align={alignCenter ? 'center' : null}
            style={style}
        >
            <div className={icon ? 'rds-flexbox align-top' : null}>
                {icon ? getIcon() : null}
                <div className='rds-flexbox direction-column'>
                    {getContent()}
                    {text && typeof text === 'string' ? (
                        <Typography type={TypographyTypes.P} variant={variant}>
                            {text}
                        </Typography>
                    ) : (
                        text
                    )}
                </div>
            </div>
        </header>
    )
}

export default Header
