import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import { labelColorRule } from '../constants/cssRules'

const StyledLabel = styled.label`
    ${props => labelColorRule(props)}
    ${props =>
        props.active
            ? `color: ${props.themeContext.main};`
            : ''}
    text-transform: uppercase;
    font-size: 0.6rem;
    font-weight: 700;
    transition: 0.2s color;
`

const Label = ({ className, active, children, align, variant }) => {
    const themeContext = useContext(ThemeContext)
    return (
        <StyledLabel
            className={className}
            align={align}
            active={active}
            themeContext={themeContext}
            variant={variant}
        >
            {children}
        </StyledLabel>
    )
}

export default Label
