import React, { useState, useEffect, useContext } from 'react'
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'
import {
    ThemeVariants,
    HeaderSizes,
    FormItemTypes,
    Icons,
} from '../../constants'
import Card from '../Card'
import Label from '../Label'
import Header from '../Header'
import Select from './Select'
import Input from '../Form/Input'
import Textarea from '../Form/Textarea'
import styled, { ThemeContext } from 'styled-components'
import { borderRadiusRule, blackValue } from '../../constants/cssRules'
import FilesPicker from './FilesPicker'
import Block from '../Block'
import MultiSelect from './MultiSelect'
import Icon from '../Icons'

const StyledTooltip = styled.span`
    position: absolute;
    display: inline-flex !important;
    padding: 2px 8px;
    background-color: ${props => blackValue(props.themeContext)};
    color: white;
    text-transform: initial;
    font-weight: 400;
    ${props => borderRadiusRule(props)}
    opacity: 0 !important;
    transition: 0.2s all;
    margin: 0 0 0 10px;
    &.active {
        opacity: 1 !important;
    }
    & .rds-icon {
        margin: 0 8px 0 0;
        width: 10px;
        height: 10px;
    }
`

const FormItem = ({
    accept,
    align,
    currentValue,
    children,
    id,
    label,
    onChange,
    onRemove,
    onClick,
    placeholder,
    required,
    size,
    type,
    values,
    value,
    tooltip,
    uploadLabel,
    fileSelectedCallback,
    fileDeselectedCallback,
    errorMessage,
    minLength,
    multiple,
    className,
    style,
    min,
    icon,
}) => {
    const themeContext = useContext(ThemeContext)
    const [processedValue, setProcessedValue] = useState(value)
    const [currentOption, setCurrentOption] = useState(null)
    const [active, setActive] = useState(false)
    const [focusing, setFocusing] = useState(false)
    const [checkboxValue, setCheckboxValue] = useState(false)

    useEffect(() => {
        if (!value || value === processedValue) return
        setProcessedValue(value)
    }, [value])

    useEffect(() => {
        if (currentValue !== null && currentValue !== undefined) {
            setCurrentOption(currentValue)
            setCheckboxValue(currentValue)
        }
    }, [currentValue])

    const handleSelect = selectedValue => {
        setCurrentOption(selectedValue)
        onChange && onChange({ target: { id, value: selectedValue } })
    }

    const handleBrowserDeselect = () => {
        document.getElementById('selectedFile').value = ''
        fileDeselectedCallback && fileDeselectedCallback()
    }

    const handleDatePick = date => {
        if (onChange) {
            onChange({ target: { id, value: date } })
        }
    }

    const handleCheckboxClick = () => {
        onChange && onChange({ target: { id, value: !checkboxValue } })
        setCheckboxValue(!checkboxValue)
    }

    const getFormItemType = () => {
        switch (type) {
            case FormItemTypes.MULTI_SELECT:
                return (
                    <MultiSelect
                        id={id}
                        onSelect={valueId => handleSelect(valueId)}
                        values={values}
                        currentValue={currentOption}
                    />
                )

            case FormItemTypes.SELECT:
                return (
                    <Select
                        values={values}
                        currentValue={currentOption}
                        id={id}
                        onSelect={valueId => handleSelect(valueId)}
                    />
                )

            case FormItemTypes.CARD_SELECT:
                return (
                    <Select
                        values={values}
                        currentValue={currentOption}
                        id={id}
                        onSelect={valueId => handleSelect(valueId)}
                        cardsUI
                    />
                )

            case FormItemTypes.FILES_PICKER:
                return (
                    <FilesPicker
                        accept={accept}
                        label={label}
                        uploadLabel={uploadLabel}
                        multiple={multiple}
                        initialFiles={currentValue}
                        onChange={onChange}
                        onRemove={onRemove}
                    />
                )

            case FormItemTypes.DATE_PICKER:
                return (
                    <Block
                        variant={ThemeVariants.GRAY}
                        roundedBorder
                        className='rds-flexbox'
                    >
                        <DatePicker
                            id={id}
                            selected={value || null}
                            onChange={handleDatePick}
                            autoComplete='off'
                            className='rds-flexbox'
                        />
                    </Block>
                )

            case FormItemTypes.CHECKBOX:
                return (
                    <Block
                        variant={ThemeVariants.GRAY}
                        className={`rounded-border_full form-item_checkbox ${
                            checkboxValue ? 'active' : ''
                        }`}
                        onClick={handleCheckboxClick}
                    >
                        <Icon
                            icon={Icons.SUCCESS}
                            variant={ThemeVariants.MAIN}
                            filled
                        />
                    </Block>
                )

            case FormItemTypes.TEXTAREA:
                return (
                    <Textarea
                        id={id}
                        className={className}
                        onClick={onClick}
                        onChange={onChange}
                        type={type}
                        min={min}
                        value={value}
                        minLength={minLength}
                        required={required}
                        placeholder={placeholder}
                        children={children}
                        onFocus={() => setActive(true)}
                        onBlur={() => setActive(false)}
                    />
                )

            default:
                return (
                    <Input
                        id={id}
                        className={className}
                        onClick={onClick}
                        onChange={onChange}
                        type={type}
                        min={min}
                        value={value}
                        minLength={minLength}
                        required={required}
                        placeholder={placeholder}
                        children={children}
                        onBlur={() => setActive(false)}
                        onFocus={() => setActive(true)}
                    />
                )
        }
    }

    if (
        type === FormItemTypes.FILE_BROWSER &&
        (!fileSelectedCallback || !handleBrowserDeselect)
    ) {
    }
    return (
        <div
            style={style}
            align={align}
            className={`form-item rds-m_top__md rds-full-block ${size || ''}`}
            onMouseOver={() => setActive(true)}
            onMouseLeave={() => !focusing && setActive(false)}
            onFocus={() => setFocusing(true)}
            onBlur={() => setFocusing(false)}
        >
            <div className={icon ? 'rds-flexbox align-center' : null}>
                {icon ? (
                    <Icon
                        className={`form-item_icon ${active ? 'active' : null}`}
                        icon={icon}
                        width={13}
                    />
                ) : null}
                <div className='rds-flexbox direction-column'>
                    <Label
                        className={
                            tooltip
                                ? `form-item_label ${active ? 'active' : null}`
                                : ''
                        }
                        active={active}
                    >
                        {label}
                        {tooltip && (
                            <StyledTooltip
                                themeContext={themeContext}
                                className={`form-item_tooltip rds-flexbox align-center ${
                                    active ? 'active' : ''
                                }`}
                            >
                                <Icon
                                    icon={Icons.VIEW}
                                    variant={ThemeVariants.WHITE}
                                    width={10}
                                />{' '}
                                {tooltip}
                            </StyledTooltip>
                        )}
                    </Label>
                </div>
            </div>
            <div className={label ? 'rds-m_top__sm' : ''}>
                {children || getFormItemType()}
            </div>
            {errorMessage && errorMessage.target && errorMessage.target === id && (
                <Card
                    style={{
                        float: 'left',
                        width: '100%',
                        padding: '6px 20px',
                        margin: '10px 0 0 0',
                    }}
                    variant={ThemeVariants.ERROR}
                >
                    <Header
                        title={errorMessage.message}
                        variant={ThemeVariants.ERROR}
                        size={HeaderSizes.XSMALL}
                    />
                </Card>
            )}
        </div>
    )
}

export default FormItem
