import React from 'react'
import styled from 'styled-components'
import { colorRule, fontSizeRule } from '../../constants/cssRules'

const StyledH4 = styled.h4`
    ${props => `
        ${colorRule(props)}
        ${fontSizeRule('12px')}
        font-weight: 300;
    `}
`

const H4 = ({
    themeContext,
    className,
    children,
    align,
    onClick,
    variant,
    style,
}) => {
    return (
        <StyledH4
            align={align}
            themeContext={themeContext}
            className={className}
            onClick={onClick && onClick}
            style={onClick ? { cursor: 'pointer', ...style } : style}
            variant={variant}
        >
            {children}
        </StyledH4>
    )
}

export default H4
