// Deps
import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import { ButtonVariants, ThemeVariants } from '../../constants'
// Comps
import Icon from '../Icons'
import { COLOR_ERROR, borderRadiusRule } from '../../constants/cssRules'

const getButtonBackgroundColor = (variant, themeSettings, isHover) => {
    const mainColor = themeSettings.main
    const BTN_BG_OPACITY = 10
    const BTN_HOVER_BG_OPACITY = 25
    const lightColor = `${mainColor}${
        isHover ? BTN_HOVER_BG_OPACITY : BTN_BG_OPACITY
    }`
    switch (variant) {
        case ButtonVariants.MAIN:
            return mainColor
        case ButtonVariants.DANGER:
            return COLOR_ERROR
        case ButtonVariants.DANGER_SECONDARY:
            return `${COLOR_ERROR}${
                isHover ? BTN_HOVER_BG_OPACITY : BTN_BG_OPACITY
            }`
        case ButtonVariants.DARK:
            return lightColor
        case ButtonVariants.TRANSPARENT:
            return `#00000000`
        case ButtonVariants.ACCEPT:
            return lightColor
        case ButtonVariants.ADD:
            return lightColor
        case ButtonVariants.EDIT:
            return lightColor
        case ButtonVariants.GOOGLE_DRIVE:
            return lightColor
        default:
            return `${mainColor}${
                isHover ? BTN_HOVER_BG_OPACITY : BTN_BG_OPACITY
            }`
    }
}

const getButtonLabelColor = (variant, themeSettings) => {
    const mainColor = themeSettings.main
    switch (variant) {
        case ButtonVariants.MAIN:
            return `white`
        case ButtonVariants.DANGER:
            return 'white'
        case ButtonVariants.DANGER_SECONDARY:
            return COLOR_ERROR
        default:
            return mainColor
    }
}

const StyledButton = styled.button`
    ${props =>
        props.themeContext.main &&
        `background: ${getButtonBackgroundColor(
            props.variant,
            props.themeContext,
        )} !important;
        color: ${getButtonLabelColor(props.variant, props.themeContext)};
        ${borderRadiusRule(props)}
        padding: 9px;
        font-weight: 600;
        transition: 0.2s all;
        ${
            props.labeled
                ? `
                    padding: 5px 20px;
                    & svg {
                        margin: 0 9px 0 0;
                    }`
                : ''
        }
        &:hover {
            background: ${getButtonBackgroundColor(
                props.variant,
                props.themeContext,
                true,
            )} !important;
            ${
                props.variant === ButtonVariants.MAIN &&
                `box-shadow: 0 10px 40px -5px ${props.themeContext.main}90;`
            }
            
        }
        * {
            pointer-events: none;
        }
        & svg {
            width: 10px;
            height: 100%;
        }`}
`

const Button = ({
    id,
    cardHoverOnly,
    type,
    label,
    variant,
    filled,
    onClick,
    style,
    icon,
    className,
}) => {
    const themeContext = useContext(ThemeContext)

    const getIconVariant = () => {
        let iconVariant = ThemeVariants.MAIN
        switch (variant) {
            case ButtonVariants.MAIN:
                iconVariant = ThemeVariants.WHITE
                break
            case ButtonVariants.DANGER:
                iconVariant = ThemeVariants.WHITE
                break
            case ButtonVariants.DANGER_SECONDARY:
                iconVariant = ThemeVariants.ERROR
                break
            case ButtonVariants.SECONDARY:
                iconVariant = ThemeVariants.MAIN
                break
            default:
                break
        }
        return iconVariant
    }

    return type === 'input' ? (
        <input type='submit' value={label} className='btn' />
    ) : (
        <StyledButton
            className={`rds-btn align-center justify-around ${
                cardHoverOnly ? 'card_hover-only_button' : ''
            } ${variant || ''} ${className || ''}`}
            themeContext={themeContext}
            id={id}
            variant={variant}
            onClick={onClick}
            type='button'
            style={style}
            labeled={label}
        >
            <div
                className='rds-flexbox align-center'
                style={{ width: 'max-content' }}
            >
                {icon ? (
                    <Icon
                        icon={icon}
                        variant={getIconVariant()}
                        filled={filled}
                    />
                ) : null}
                <span>{label}</span>
            </div>
        </StyledButton>
    )
}

export default Button
