import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'

const StyledIsoLogo = styled.div`
    background: ${props =>
        props.themeContext &&
        `linear-gradient(-45deg, ${props.themeContext.main} 30%, ${props.themeContext.secondary} 65%)`};
`

const Isologo = ({ children, className, id }) => {
    const themeContext = useContext(ThemeContext)
    return (
        <StyledIsoLogo
            id={id}
            themeContext={themeContext}
            className={`rds-isologo ${className}`}
        >
            {children}
        </StyledIsoLogo>
    )
}

export default Isologo
