import React, { useEffect, useState, useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import Tag from './Tag'
import { ThemeVariants, TagVariants } from '../constants'
import Card from './Card'

const getColorVariant = props => {
    if (props.variant === ThemeVariants.SECONDARY) {
        return props.themeContext.main
    } else if (props.variant === ThemeVariants.ERROR) {
        return '#ffffff7a'
    } else {
        return props.themeContext.secondary
    }
}

const StyledCodeLinePortion = styled.span`
    color: ${props => getColorVariant(props)};
`

const StyledCodeLine = styled.p`
    font-weight: 400;
    font-size: 13px;
    color: ${props => getColorVariant(props)};
`

const CodeBlock = ({ children, className }) => {
    const themeContext = useContext(ThemeContext)
    const [codeLines, setCodeLines] = useState([])

    const milisecs = new Date().getMilliseconds()
    const randomId = `code-${Math.random() * milisecs}`

    useEffect(() => {
        const codeEl = document.getElementById(randomId)
        const splitted = children.split('\n')
        if (splitted.length > 1) {
            codeEl.classList.add('rds-code_multiple-line')
        }
        const newCodeLines = []
        splitted.forEach((s, i) => {
            newCodeLines.push(s)
        })
        setCodeLines(newCodeLines)
    }, [children])

    const handleCopy = () => {
        var textArea = document.createElement('textarea')
        textArea.value = children

        // Avoid scrolling to bottom
        textArea.style.top = '-100%'
        textArea.style.left = '-100%'
        textArea.style.position = 'fixed'

        document.body.appendChild(textArea)
        textArea.focus()
        textArea.select()
        // For mobile devices
        textArea.setSelectionRange(0, 99999)
        // Copy the text inside the text field
        document.execCommand('copy')
    }

    const getVariant = cl => {
        const trimmed = cl.trim()
        let variant
        if (trimmed.substr(0, 2) === '<!' || trimmed.substr(0, 2) === '//') {
            variant = ThemeVariants.ERROR
        } else if (
            (trimmed.substr(0, 1) === '<' ||
                trimmed.substr(0, 1) === '>' ||
                trimmed.substr(0, 2) === '/>') &&
            trimmed.substr(1, 1) !== '!'
        ) {
            variant = ThemeVariants.SECONDARY
        }
        return variant
    }

    const highlightPortion = (cl, portion) => {
        const portionLine = cl.split(portion)
        if (portionLine.length > 1) {
            return (
                <StyledCodeLine themeContext={themeContext}>
                    <StyledCodeLinePortion
                        themeContext={themeContext}
                        variant={ThemeVariants.SECONDARY}
                    >
                        {portion}
                    </StyledCodeLinePortion>
                    <StyledCodeLinePortion themeContext={themeContext}>
                        {portionLine[1]}
                    </StyledCodeLinePortion>
                </StyledCodeLine>
            )
        }
    }

    const getContent = cl => {
        const importPortion = highlightPortion(cl, 'import')
        if (importPortion) {
            return importPortion
        }
        const npmPortion = highlightPortion(cl, 'npm')
        if (npmPortion) {
            return npmPortion
        }
        const yarnPortion = highlightPortion(cl, 'yarn')
        if (yarnPortion) {
            return yarnPortion
        }
        return cl
    }

    return (
        <Card
            variant={ThemeVariants.DARK}
            className={`rds-code ${className || ''}`}
        >
            <code id={randomId}>
                {codeLines.map((cl, i) => {
                    if (
                        // Awfull fix
                        (!cl || !cl.length || cl === '                ') &&
                        i === codeLines.length - 1
                    ) {
                        return null
                    }
                    return (
                        <StyledCodeLine
                            themeContext={themeContext}
                            key={i}
                            variant={getVariant(cl)}
                        >
                            {getContent(cl)}
                        </StyledCodeLine>
                    )
                })}
            </code>
            <Tag
                onClick={handleCopy}
                variant={TagVariants.DARK}
                className='rds-code_copy'
            >
                Copy code
            </Tag>
        </Card>
    )
}

export default CodeBlock
