import React, { useState } from 'react'
import Typography from './Typography'
import { TypographyTypes, Icons, FormItemSizes } from '../constants'
import Icon from './Icons'
import ComponentError from './States/ComponentError'

const Rater = ({
    values,
    initial,
    onChange,
    showLabel,
    size,
    className,
    id,
}) => {
    const [currentRate, setCurrentRate] = useState(initial || 2)

    const handleClick = value => {
        onChange(value)
        setCurrentRate(value)
    }

    if (!values) {
        return (
            <ComponentError
                componentName='Rater'
                errorMessage='A "values" prop needs to be provided'
            />
        )
    }
    return (
        <div className={`rds-rate ${className || ''}`} id={id}>
            {showLabel ? (
                <div>
                    <Typography type={TypographyTypes.H3}>
                        {values[currentRate]}
                    </Typography>
                </div>
            ) : null}
            <div className=' rds-flexbox align-center'>
                {values.map((s, i) => (
                    <div
                        key={i}
                        onClick={onChange ? () => handleClick(i) : null}
                        className={`rds-rate_selector-item ${
                            onChange ? 'rds-rate_selector-item_interactive' : ''
                        } ${currentRate >= i ? 'active' : ''}`}
                    >
                        <Icon
                            icon={Icons.STAR}
                            filled={currentRate >= i}
                            width={size === FormItemSizes.BIG ? 30 : null}
                        />
                    </div>
                ))}
            </div>
        </div>
    )
}

export default Rater
